import styled from "styled-components";
import { useState, useEffect } from "react";

function InfoStarRate({ rate }: { rate: number }) {
  const AVR_RATE = rate * 10; //   .     .
  const STAR_IDX_ARR = ["first", "second", "third", "fourth", "last"]; //       id    .         .
  const [ratesResArr, setRatesResArr] = useState([0, 0, 0, 0, 0]); //   .
  const calcStarRates = () => {
    let tempStarRatesArr = [0, 0, 0, 0, 0]; //  .
    let starVerScore = (AVR_RATE * 70) / 100; //     width 14  70. 100    .
    let idx = 0;
    while (starVerScore > 14) {
      // 14 starVerScore      width .       tempStarRatesArr[idx] = starVerScore;  .
      tempStarRatesArr[idx] = 14;
      idx += 1; //  0   .
      starVerScore -= 14;
    }
    tempStarRatesArr[idx] = starVerScore;
    return tempStarRatesArr; //  80 [14, 14, 14, 14, 0]  ?
  };
  useEffect(() => {
    setRatesResArr(calcStarRates); //        .
  }, []);
  return (
    <StarRateWrap>
      {STAR_IDX_ARR.map((item, idx) => {
        return (
          <span className="star_icon" key={`${item}_${idx}`}>
            {/*    svg . , svg  ' '   . */}
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="2vw"
              height="39"
              viewBox="0 0 14 13"
              fill="#cacaca"
            >
              {/* clip-path       . */}
              {/* id         */}
              <clipPath id={`${item}StarClip`}>
                {/*  width svg viewBox . width 14     7   . */}
                {/*      . */}
                <rect width={`${ratesResArr[idx]}`} height="39" />
              </clipPath>
              <path
                id={`${item}Star`}
                d="M9,2l2.163,4.279L16,6.969,12.5,10.3l.826,4.7L9,12.779,4.674,15,5.5,10.3,2,6.969l4.837-.69Z"
                transform="translate(-2 -2)"
              />
              {/* clip-path   .     ,  id    .  */}

              <use
                clipPath={`url(#${item}StarClip)`}
                href={`#${item}Star`}
                fill="#ffd400"
              />
            </svg>
          </span>
        );
      })}
    </StarRateWrap>
  );
}

export default InfoStarRate;

const StarRateWrap = styled.div`
  display: flex;
  align-items: center;

  .star_icon {
    display: inline-flex;
    margin-right: 5px;
  }
`;
